    font-family: "Parkinsans";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    
}

:root {
    --color-bg: #2c2727;
    --color-card: #d3b7b7;
    --color-content: #d23c3c;
    --color-border: #9b1307;
    --color-btn: #33c036;
}

*, *::before, *::after {
    box-sizing: border-box;
  }

* {
    margin: 0;
  }

body {
    line-height: 1.5;
    background-color: var(--color-bg);
    background-position: center;
    font-family: Parkinsans;
    font-weight: 600;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }

input, button, textarea, select {
    font: inherit;
  }

  header {
    display: flex;
    background-color: var(--color-content);
    justify-content: center;
    padding-top: 8px;
    margin-bottom: 32px;
  }

nav {
    display: flex;
    justify-content: space-evenly;
    width: 20%;
}
nav > button {
    padding: 8px 16px;
    border-radius: 20px 20px 0 0;
    border: 1px solid black;
    border-bottom: none;
    background-color: var(--color-content);
}

nav > button:hover {
    cursor: pointer;
}

.active {
    background-color: var(--color-btn);
}

#content {
    width: 50%;
    margin: 0 auto;
    background-color: var(--color-content);
    border-radius: 22px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.main-heading,
.menu-heading,
.container {
    border: 5px solid var(--color-border);
    background-color: var(--color-card);
    border-radius: 22px;
}

.main-heading,
.menu-heading {
    padding: 8px;
    text-align: center;
}

.main-heading {
    width: 75%;
    margin-bottom: 124px;
    font-size: 2.8em;
    font-weight: bolder;
    color: var(--color-content);
    text-shadow: 4px 2px 5px black;
}

.menu-heading {
    width: 50%;
    font-size: 1.8em;
    font-weight: 900;
}

.container {
    width: 55%;
    /* Prevents the item's pic from squishing */
    min-height: 222px;
    padding: 44px 24px 8px;
    display: flex;
    flex-direction: column;
}

.home .container {
    padding: 44px 34px 28px;
}

.menu,
.home,
.menu-section,
.contacts {
    display: flex;
    flex-direction: column;
}

.menu, 
.home {
    row-gap: 64px;
}

.home,
.menu-section,
.contacts {
    align-items: center;
}

.menu-section,
.contacts {
    width: 100%;
    row-gap: 24px;
}

.item-title {
    font-size: 1.4em;
    font-weight: 700;
    margin-bottom: 24px;
}

.home .item-title {
    margin-bottom: 8px;
}

.item-description, 
.item-phone,
.item-mail,
.item-price {
    padding-left: 32px;
}

.item-hours {
    display: flex;
    justify-content: center;
}
.item-price {
    font-weight: 800;
    font-size: 1.2em;
}

.home-description {
    font-size: 1.2em;
}

img {
    max-width: 80%;
    border-radius: 111px;
}

.item-img {
    border: 2px solid orange;
    width: 264px;
    height: 264px;
    border-radius: 24px;
    align-self: flex-end;
    background-position: center;
    background-size: cover;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,4CAA4C;;AAEhD;;AAEA;IACI,mBAAmB;IACnB,qBAAqB;IACrB,wBAAwB;IACxB,uBAAuB;IACvB,oBAAoB;AACxB;;AAEA;IACI,sBAAsB;EACxB;;AAEF;IACI,SAAS;EACX;;AAEF;IACI,gBAAgB;IAChB,iCAAiC;IACjC,2BAA2B;IAC3B,uBAAuB;IACvB,gBAAgB;IAChB,kBAAkB;IAClB,mCAAmC;AACvC;;AAEA;IACI,cAAc;IACd,eAAe;EACjB;;AAEF;IACI,aAAa;EACf;;EAEA;IACE,aAAa;IACb,sCAAsC;IACtC,uBAAuB;IACvB,gBAAgB;IAChB,mBAAmB;EACrB;;AAEF;IACI,aAAa;IACb,6BAA6B;IAC7B,UAAU;AACd;AACA;IACI,iBAAiB;IACjB,4BAA4B;IAC5B,uBAAuB;IACvB,mBAAmB;IACnB,sCAAsC;AAC1C;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,UAAU;IACV,cAAc;IACd,sCAAsC;IACtC,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;;;IAGI,qCAAqC;IACrC,mCAAmC;IACnC,mBAAmB;AACvB;;AAEA;;IAEI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,oBAAoB;IACpB,gBAAgB;IAChB,mBAAmB;IACnB,2BAA2B;IAC3B,8BAA8B;AAClC;;AAEA;IACI,UAAU;IACV,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,UAAU;IACV,2CAA2C;IAC3C,iBAAiB;IACjB,sBAAsB;IACtB,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;;;;IAII,aAAa;IACb,sBAAsB;AAC1B;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;;IAGI,mBAAmB;AACvB;;AAEA;;IAEI,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;;;;IAII,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,oBAAoB;AACxB;;AAEA;IACI,wBAAwB;IACxB,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,oBAAoB;IACpB,2BAA2B;IAC3B,sBAAsB;AAC1B","sourcesContent":["@font-face {\n    font-family: \"Parkinsans\";\n    src: url(./Parkinsans-VariableFont_wght.ttf);\n    \n}\n\n:root {\n    --color-bg: #2c2727;\n    --color-card: #d3b7b7;\n    --color-content: #d23c3c;\n    --color-border: #9b1307;\n    --color-btn: #33c036;\n}\n\n*, *::before, *::after {\n    box-sizing: border-box;\n  }\n\n* {\n    margin: 0;\n  }\n\nbody {\n    line-height: 1.5;\n    background-color: var(--color-bg);\n    background-position: center;\n    font-