import pasta from "./pasta.jpg";

export function loadHome() {
const contentDiv = document.querySelector("#content");

const homeDiv = document.createElement("div");
homeDiv.classList.add("home");

const restaurantName = document.createElement("h1");
restaurantName.classList.add("main-heading");
restaurantName.textContent = "Casa Bonta'";

// Credits to mariya_m @ pixabay
const pastaImage = document.createElement("img");
pastaImage.src = pasta;


const homeDescription = document.createElement("p");
homeDescription.classList.add("home-description");
homeDescription.textContent = "Our restaurant is the perfect place to immerse yourself into an environment befitting to enthusiasts of Italian cuisine and culture. We bring to the table some of the most notorious Italian dishes, including Amatriciana, Carbonara, Lasagna and many more."

const hours = document.createElement("div");
hours.classList.add("container");

const hoursTitle = document.createElement("h2");
hoursTitle.classList.add("item-title");
hoursTitle.textContent = "Hours";

const hoursMon = document.createElement("p");
hoursMon.classList.add("item-hours");
hoursMon.textContent = "Monday: 12am - 10pm";

const hoursTue = document.createElement("p");
hoursTue.classList.add("item-hours");
hoursTue.textContent = "Tuesday: 12am - 10pm";

const hoursWed = document.createElement("p");
hoursWed.classList.add("item-hours");
hoursWed.textContent = "Wednesday: 12am - 10pm";

const hoursThur = document.createElement("p");
hoursThur.classList.add("item-hours");
hoursThur.textContent = "Thursday: 12am - 10pm";

const hoursFri = document.createElement("p");
hoursFri.classList.add("item-hours");
hoursFri.textContent = "Friday: 12am - 10pm";

const hoursSat = document.createElement("p");
hoursSat.classList.add("item-hours");
hoursSat.textContent = "Saturday: 1pm - 12pm";

const hoursSun = document.createElement("p");
hoursSun.classList.add("item-hours");
hoursSun.textContent = "Sunday: 1pm - 12pm";

hours.append(hoursTitle, hoursMon, hoursTue, hoursWed, hoursThur, hoursFri, hoursSat, hoursSun);

homeDiv.append(pastaImage, homeDescription, hours)

contentDiv.append(restaurantName, homeDiv);
}