// Contacts are objects that can be rendered to the site
// This makes it easier to eventually add more of them
class Contact {
    constructor(name, description, phone, mail) {
        this.name = name;
        this.description = description;
        this.phone = phone;
        this.mail = mail;

        this.container = document.createElement("div");
        this.container.classList.add("container");
    }

    getContainer = () => this.container;

    render = () => {
        const contactName = document.createElement("h2");
        contactName.classList.add("item-title");
        contactName.textContent = this.name;

        const contactDescription = document.createElement("p");
        contactDescription.classList.add("item-description");
        contactDescription.textContent = this.description;

        const contactPhone = document.createElement("p");
        contactPhone.classList.add("item-phone");
        contactPhone.textContent = this.phone;

        const contactMail = document.createElement("p");
        contactMail.classList.add("item-mail");
        contactMail.textContent = this.mail;

        this.container.append(contactName, contactDescription, contactPhone, contactMail);
    }
}

export function loadContacts() {
    const contentDiv = document.querySelector("#content");

    const contactsDiv = document.createElement("div");
    contactsDiv.classList.add("contacts");

    const contactHeading = document.createElement("h1");
    contactHeading.classList.add("main-heading");
    contactHeading.textContent = "Contact Us";

    // Contacts are added here
    const contacts = [
        new Contact("Marco Rossi", "Chef", "555-555-5554", "Real@notFake.com"),
        new Contact("Alex Rosetti", "Waitress", "555-555-5555", "JustARealEmail@notFake.com"),
        new Contact("Luca Albino", "Manager", "555-555-5556", "perfectlyRealEmail@notFake.com"),
    ];

    // Renders each contact and appends it to the contacts container
    contacts.forEach(contact => {
            contact.render();
            contactsDiv.append(contact.getContainer());
        }
    )

    contentDiv.append(contactHeading, contactsDiv);

}

