
// Allows to create multiple menu-sections
class MenuSection {
    constructor(name) {
        this.name = name;

        this.container = document.createElement("div");
        this.container.classList.add("menu-section");
    }

    setHeading = () => {
        const heading = document.createElement("h2");
        heading.classList.add("menu-heading");
        heading.textContent = this.name;

        this.container.appendChild(heading);
    }

    getContainer = () => this.container;

    // Method that allows to add an item to this menu-section
    addMenuItem = (item, description, price, image) => {
        const itemDiv = document.createElement("div");
        itemDiv.classList.add("container");
    
        const itemTitle = document.createElement("p");
        itemTitle.classList.add("item-title");
        itemTitle.textContent = item;
    
        const itemDescription = document.createElement("p");
        itemDescription.classList.add("item-description");
        itemDescription.textContent = description;

        const itemPrice = document.createElement("p");
        itemPrice.classList.add("item-price");
        itemPrice.textContent = `$${price}`;
    
    
        const itemImage = document.createElement("img");
        itemImage.classList.add("item-img");
        itemImage.style.backgroundImage = `url(${image})`;

        itemDiv.append(itemTitle, itemDescription, itemPrice, itemImage);

        this.container.append(itemDiv);
    }    
    
}

